<template lang="pug">
  li(v-if="item.data[0].media_type=='image'")
    .titulo
      h2 Title: {{item.data[0].title}}
    figure
      img(:src="item.links[0].href")
    //- p {{item.data[0].description}}
    p Para saber más: 
      span.searchAbout(v-for="t in item.data[0].keywords",
                   @click="searchAbout(t)",
                   href="#") {{t}} | 
    megusta(:item="item")

</template>

<script>
import nasa from '@/services/api/fetchEarth.js'
import Megusta from '@/components/Megusta.vue'

export default {
  name:'Images',
  props:['item'],
  data(){
    return {
    }
  },
  components:{ Megusta },
  methods:{
    searchAbout(t){
      this.$bus.$emit('search-about', t)
    }
  }
}
</script>

<style lang="sass" scoped>
  
  li
    background: #EEECEC
    width: 45%
    overflow: hidden
    margin: 10px 
    border: 1px solid rgb(211, 211, 211)

    figure
      height: 200px
      width: 100%
      margin: 0
      img
        // width: 90%
        height: 100%

    .titulo
      height: 100px  
      margin: 0
      display: flex
      align-items: center
      justify-content: center
      
      h2
        margin: 0
    
  .searchAbout
    text-decoration: underline
    color: #1D654B !important
    cursor: pointer
  
  
  @media screen and (max-width: 800px)
    li
      width: 90%  
</style>



